<script>
export default {
  name: 'render-custom-component',
  props: {
    /**
     * @description v-model双向绑定的值
     */
    value: {
      required: true
    },
    /**
     * @description 传入的组件名
     */
    componentName: {
      required: true
    },
    /**
     * @description 是否双向绑定
     */
    sync:{
        required:true,
        default:true,
    }
  },
  render (h) {
    let self = this
    console.log(self.sync)
    return h(this.componentName, {
      props: {
        value: self.value
      },
      on: self.sync?{
        input: function (event) {
          self.$emit('input', event)
        }
      }:{}
    })
  }
}
</script>